"use client";
import { useState, useEffect } from "react";
import { useForm } from "react-hook-form";
import {
  Loader2,
  Image,
  Star,
  Calendar,
  Clapperboard,
  AlertCircle,
} from "lucide-react";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";

import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { MultiSelect } from "@/components/multi-select";
import { GENRES, RATINGS } from "@/lib/constants";
import { cn } from "@/lib/utils";

export default function EditMovieForm({ movie, open, onCancel }) {
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm();
  const [selectedGenres, setSelectedGenres] = useState([]);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState("");

  useEffect(() => {
    if (movie) {
      reset({
        title: movie.title,
        year: movie.year,
        plot: movie.plot,
        poster: movie.poster,
        rated: movie.rated,
        imdbRating: movie.imdb?.rating || 0,
      });
      setSelectedGenres(movie.genres || []);
    }
  }, [movie, reset]);

  const onSubmit = async (data) => {
    setIsSubmitting(true);
    setError("");

    try {
      const payload = {
        ...data,
        genres: selectedGenres,
        imdb: { rating: parseFloat(data.imdbRating) || 0 },
      };

      const response = await fetch(`/api/movies/${movie._id}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || "Failed to update movie");
      }

      onCancel(); // Close dialog
      // Optionally refresh data or show success message
    } catch (err) {
      setError(err.message);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={onCancel}>
      <DialogContent className="max-w-2xl">
        <DialogHeader>
          <div className="flex items-center gap-3">
            <Clapperboard className="h-6 w-6" />
            <DialogTitle className="text-2xl">Edit Movie</DialogTitle>
          </div>
        </DialogHeader>

        <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {/* Title */}
            <div className="space-y-2">
              <Label>Title</Label>
              <Input
                {...register("title", { required: true })}
                placeholder="Movie title"
              />
              {errors.title && (
                <p className="text-red-600 text-sm flex items-center gap-1">
                  <AlertCircle className="h-4 w-4" />
                  Title is required
                </p>
              )}
            </div>

            {/* Year */}
            <div className="space-y-2">
              <Label>Release Year</Label>
              <Input
                type="number"
                {...register("year", { required: true, min: 1900 })}
                placeholder="Release year"
              />
              {errors.year && (
                <p className="text-red-600 text-sm flex items-center gap-1">
                  <AlertCircle className="h-4 w-4" />
                  Valid year required
                </p>
              )}
            </div>
          </div>

          {/* Plot */}
          <div className="space-y-2">
            <Label>Plot Summary</Label>
            <Textarea
              {...register("plot", { required: true, minLength: 50 })}
              className="min-h-[120px]"
              placeholder="Movie plot summary"
            />
            {errors.plot && (
              <p className="text-red-600 text-sm flex items-center gap-1">
                <AlertCircle className="h-4 w-4" />
                Plot must be at least 50 characters
              </p>
            )}
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {/* Genres */}
            <div className="space-y-2">
              <Label>Genres</Label>
              <MultiSelect
                list={GENRES.map((g) => ({ label: g, value: g }))}
                selectedItems={selectedGenres}
                onValueChange={setSelectedGenres}
                placeholder="Select genres..."
              />
              {selectedGenres.length === 0 && (
                <p className="text-red-600 text-sm flex items-center gap-1">
                  <AlertCircle className="h-4 w-4" />
                  Select at least one genre
                </p>
              )}
            </div>

            {/* Rating */}
            <div className="space-y-2">
              <Label>Content Rating</Label>
              <select
                {...register("rated")}
                className="w-full p-2 border rounded"
                defaultValue={movie?.rated}
              >
                {RATINGS.map((rating) => (
                  <option key={rating} value={rating}>
                    {rating}
                  </option>
                ))}
              </select>
            </div>
          </div>

          {/* IMDb Rating */}
          <div className="space-y-2">
            <Label>IMDb Rating</Label>
            <Input
              type="number"
              step="0.1"
              min="0"
              max="10"
              {...register("imdbRating")}
              placeholder="IMDb rating"
            />
          </div>

          {/* Poster URL */}
          <div className="space-y-2">
            <Label>Poster URL</Label>
            <Input
              type="url"
              {...register("poster", { required: true })}
              placeholder="https://example.com/poster.jpg"
            />
            {errors.poster && (
              <p className="text-red-600 text-sm flex items-center gap-1">
                <AlertCircle className="h-4 w-4" />
                Valid URL required
              </p>
            )}
          </div>

          {error && (
            <div className="text-red-600 p-3 bg-red-50 rounded flex items-center gap-2">
              <AlertCircle className="h-5 w-5" />
              {error}
            </div>
          )}

          <div className="flex justify-end gap-4 pt-6">
            <Button
              type="button"
              variant="outline"
              onClick={onCancel}
              disabled={isSubmitting}
            >
              Cancel
            </Button>
            <Button type="submit" disabled={isSubmitting}>
              {isSubmitting ? (
                <>
                  <Loader2 className="animate-spin mr-2 h-4 w-4" />
                  Saving...
                </>
              ) : (
                "Save Changes"
              )}
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}

